#root {
  height: 100%;
  width: 100%;
}
.main-wrapper {
  height: 100%;
  width: 100%;
  background: linear-gradient(90deg, hsla(0, 0%, 100%, 0.05) 10%, transparent 0), linear-gradient(hsla(0, 0%, 100%, 0.05) 10%, transparent 0), radial-gradient(#072367 0, #070434 100%);
  background-size: 20px 20px,20px 20px,25% 100%;
}
.main-wrapper .header-wrapper {
  height: 68px;
  position: relative;
}
.main-wrapper .header-wrapper .home {
  position: absolute;
  left: 0;
  top: 0;
  height: 68px;
  width: 110px;
  line-height: 68px;
  background: url(./rect.png) no-repeat left top;
}
.main-wrapper .header-wrapper .other {
  position: absolute;
  left: 64px;
  top: 15px;
  height: 52px;
  width: calc(100% - 64px);
  background: url(./pc_titleline.png) no-repeat left top;
  background-size: 100% 100%;
}
.main-wrapper .header-wrapper .other-title {
  color: #00cfff;
  font-size: 18px;
  margin-left: 5%;
  float: left;
}
@media (min-width: 1300px) {
  .main-wrapper .header-wrapper .other-title {
    font-size: 19px;
  }
}
@media (min-width: 1400px) {
  .main-wrapper .header-wrapper .other-title {
    font-size: 20px;
  }
}
@media (min-width: 1500px) {
  .main-wrapper .header-wrapper .other-title {
    font-size: 21px;
  }
}
@media (min-width: 1600px) {
