    font-size: 26px;
  }
}
.main-wrapper .header-wrapper .other-menu {
  position: relative;
  width: 180px;
  height: 40px;
  background: url(./pc_select.png) no-repeat left top;
  background-size: 100% 100%;
  float: left;
  margin-left: 7%;
  margin-top: 11px;
  text-align: center;
  color: #fff;
  line-height: 40px;
  font-size: 18px;
  cursor: pointer;
}
@media (min-width: 1500px) {
  .main-wrapper .header-wrapper .other-menu {
    margin-left: 7.5%;
