}
.main-wrapper .header-wrapper .other-menu:hover .other-sub-menu {
  display: block;
}
.main-wrapper .header-wrapper .other-menu .other-sub-menu {
  display: none;
  position: absolute;
  z-index: 10;
  left: 10px;
  top: 40px;
  width: 138px;
  height: 369px;
  background: rgba(6, 36, 94, 0.8);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.7);
  border: 1px solid #00cfff;
}
.main-wrapper .header-wrapper .other-menu .other-sub-menu .item {
  font-size: 14px;
}
.main-wrapper .header-wrapper .other-menu .other-sub-menu .item.active {
  color: #00cfff;
}
.main-wrapper .header-wrapper .other-menu-type {
  margin-left: 0;
  font-size: 12px;
}
.main-wrapper .header-wrapper .other-menu-type .other-sub-menu-type {
  height: 246px;
}
