.main-wrapper .header-wrapper .carousel-title {
  position: absolute;
  top: 0;
  right: 0;
  width: 13%;
  height: 45px;
  font-size: 18px;
  color: #fff;
  line-height: 45px;
  text-align: center;
}
.main-wrapper .body-wrapper {
  height: calc(100vh - 68px);
  padding: 4px 12px;
  box-sizing: border-box;
  overflow: hidden;
}
.main-wrapper .body-wrapper .item-wrapper {
  float: left;
  width: 25%;
  height: calc((100vh - 68px)/3 + 0.3px);
  padding: 8px 10px;
  box-sizing: border-box;
}
.main-wrapper .body-wrapper .item-wrapper .item-box {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}
.main-wrapper .body-wrapper .item-wrapper .item-box .item-title {
  height: 30px;
  background: url(./title.png) no-repeat left top;
  margin-bottom: -3px;
}
.main-wrapper .body-wrapper .item-wrapper .item-box .item-title span {
  line-height: 30px;
  color: #00CFFF;
  display: inline-block;
  padding-left: 30px;
  width: 250px;
  text-align: center;
}
.main-wrapper .body-wrapper .item-wrapper .item-box .item-chart {
  flex: 1;
  background-color: rgba(0, 207, 255, 0.1);
  opacity: 0.8734;
}
.main-wrapper .body-wrapper .item-wrapper .item-box .item-info {
  height: 40px;
  line-height: 40px;
  background-color: rgba(0, 207, 255, 0.1);
  opacity: 0.8734;
  color: #fff;
  font-size: 14px;
  text-indent: 20px;
}
.main-wrapper .body-wrapper .item-wrapper .item-box::after {
  content: "";
  position: absolute;
  border-top: 5px solid transparent;
  border-left: 5px solid transparent;
  border-right: 5px solid #00cfff;
  border-bottom: 5px solid #00cfff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
  border-radius: 3px 0 0;
  width: 0px;
  height: 0px;
  right: 0px;
  bottom: 0px;
}
